html {
  height: 100%;
  min-height: 100%;
  box-sizing: border-box;
  font-size: 16px; }

body {
  height: 100%;
  min-height: 100%;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
  opacity: 0;
  transition: opacity 0.3s;
  will-change: opacity; }
  .is-page-loaded body {
    opacity: 1; }

.site-wrapper {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  overflow-x: hidden;
  overflow-y: hidden;
  min-height: 100%;
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5) 0%, transparent 50%, rgba(0, 0, 0, 0.5) 100%), url("../images/pattern.jpg");
  background-position: 0 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #000; }

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  max-width: 1120px;
  background-color: transparent; }

.main-title {
  margin-top: 0;
  margin-bottom: 27px;
  padding-top: 41px;
  width: 100%;
  font-family: "Exo_2_Thin", sans-serif;
  font-weight: 100;
  font-size: 36px;
  line-height: 1;
  text-align: center;
  text-shadow: 0 1px 0 #000;
  color: #fff; }
  @media (max-width: 380px) {
    .main-title {
      padding-right: 5%;
      padding-left: 5%; } }

